/**
 * Theme: default
 */
:root {
  font-family: system-ui, -apple-system, "Segoe UI", "Roboto", "Ubuntu", "Cantarell", "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  line-height: 1.5;
  font-weight: 400;
  font-size: 16px;
  border-radius: 0.25rem;
  border-width: 1px;
  outline-width: 3px;
}

a {
  color: 195;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

small {
  font-size: 0.875em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.75rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 1.125rem;
}

[type=checkbox],
[type=radio] {
  border-width: 2px;
}

[type=checkbox][role=switch] {
  border-width: 3px;
}

thead th,
thead td,
tfoot th,
tfoot td {
  border-width: 3px;
}

:not(thead, tfoot) > * > td {
  font-size: 0.875em;
}

pre,
code,
kbd,
samp {
  font-family: "Menlo", "Consolas", "Roboto Mono", "Ubuntu Monospace", "Noto Mono", "Oxygen Mono", "Liberation Mono", monospace, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

kbd {
  font-weight: bolder;
}

.text-white {
  color: #fff;
}

.text-blue {
  color: blue;
}

